export default {
	data: function() {
		return {
			// 记录当前索引
			recordList: [],
			status: false,
			current: 0,
			tablist: ["年","月","日","时","分"],
			// 日期数组
			year: '',
			mouth: '',
			day: '',
			hour: '',
			minutes: '',
		}
	},
	props:['timeList'],
	methods: {
		bindChange(e) {
			if(this.current === 0) {
				this.year = e.detail.value;
			}
			console.log(this.recordList)
		},
		
		justClose() {
			this.status = true;
			this.$emit('showStatus', this.status)
		},
		
		reSelected() {
			this.status = true
			this.$emit('showStatus', this.status)
			this.$emit('listenChild', this.recordList)
		},
		
		clickTab(index) {
			this.current = index;
			console.log(this.current)
		},
	}
}
